import React from 'react'
import PrefDiscription from './ProfileContent/PrefDiscription'
import { Parallax, ParallaxLayer } from 'react-spring/renderprops-addons'
import { Space } from 'antd'
import {connect } from "react-redux"
import Skills from "./ProfileContent/IntrestAndSkills"
// Little helpers ...
const url = (name, wrap = false) =>
  `${
    wrap ? 'url(' : ''
  }https://awv3node-homepage.surge.sh/build/assets/${name}.svg${
    wrap ? ')' : ''
  }`




//-------------------------------- Map StateTo Props ----------------------------//

const mapStateToProps =(state)=>({
  AboutMe :state.AboutMe.About
})

class Profile extends React.Component {
  constructor(props){
    super(props)
  }
  render() {

    return (
      <Parallax ref={(ref) => (this.parallax = ref)} pages={4}  >
          
        <ParallaxLayer
          offset={0}
          speed={1}
          style={{ backgroundColor: '#35528C' }}
        />
        <ParallaxLayer
          offset={1}
          speed={1}
          style={{ backgroundColor: '#282C34' }}
        />
        <ParallaxLayer
          offset={2}
          speed={1}
          style={{ backgroundColor: '#101010' }}
        />
        <ParallaxLayer
          offset={3}
          speed={1}
          style={{ backgroundColor: '#87BCDE' }}
        />

        <ParallaxLayer
          offset={0}
          speed={0}
          factor={4}
          style={{
            backgroundImage: url('stars', true),
            backgroundSize: 'cover',
          }}
        />

        <ParallaxLayer
          offset={3}
          speed={-0.4}
          style={{
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
            pointerEvents: 'none',
          }}
        >
          <img
            src={
              'https://webstockreview.net/images/clipart-happy-business-person-6.png'
            }
            style={{ marginTop: 20, width: '60%' }}
          />
        </ParallaxLayer>

        {/* <ParallaxLayer
          offset={2}
          speed={-0.3}
          style={{
            backgroundSize: '80%',
            backgroundPosition: 'center',
            backgroundImage: url('clients', true),
          }}
        /> */}

        <ParallaxLayer
          offset={0}
          speed={0.1}
          onClick={() => this.parallax.scrollTo(1)}
          style={{
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
            marginRight:"30%",
            marginLeft:"7%",
          }}
        >
          <div style={{width:"50%"}} >
            <div style={{alignContent:"center" ,display:"flex",flexDirection:"row" }}>
              
              <PrefDiscription AbouMe={this.props.AboutMe[0]}></PrefDiscription>
               <img src={"https://pngimg.com/uploads/question_mark/question_mark_PNG129.png"} style={{width:"30%", height:"25%"}} />
            </div>

           
          </div>
        </ParallaxLayer>

        <ParallaxLayer
          offset={1}
          speed={0.1}
          onClick={() => this.parallax.scrollTo(2)}
          style={{
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
          }}
        >
           <div style={{alignContent:"center" ,display:"flex",flexDirection:"row" ,width:"50%"}}>
         <Skills data={this.props.AboutMe[1]}></Skills>
     
          </div>
              
        </ParallaxLayer>

        <ParallaxLayer
          offset={2}
          speed={0.1}
          style={{
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
          }}
          onClick={() => this.parallax.scrollTo(3)}
        >
          <img src={url('clients-main')} style={{ width: '40%' }} />
        </ParallaxLayer>

        <ParallaxLayer
          offset={3}
          speed={0}
          style={{
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
          }}
          onClick={() => this.parallax.scrollTo(0)}
        >
          <img src={url('bash')} style={{ width: '40%' }} />
        </ParallaxLayer>
      </Parallax>
    )
  }
}

export default connect(mapStateToProps)( Profile)
