
import React, { useState, useRef } from 'react'
import { useTransition, useSpring, useChain, config } from 'react-spring'
import { Global, Container, Item } from './styles'
import data from './data'

///---------------------- Redux with Hooks --------------------//
import { useSelector } from 'react-redux'


 function Projects() {
  const [open, set] = useState(false)

  const springRef = useRef()
  const { size, opacity, ...rest } = useSpring({
    ref: springRef,
    config: config.stiff,
    from: { size: '15%', background: 'green' ,margin:"50% auto" ,marginTop:"20%"},
    to: { size: open ? '100%' : '15%', background: open ? 'rgba(255, 255, 255, 0.1)' : 'green',margin:open ?"0 auto":"20%" , marginTop:open ?"0":"20%" }
  })

  const transRef = useRef()
  const transitions = useTransition(open ? data : [], item => item.name, {
    ref: transRef,
    unique: true,
    trail: 400 / data.length,
    from: { opacity: 0, transform: 'scale(0)' },
    enter: { opacity: 1, transform: 'scale(1)' },
    leave: { opacity: 0, transform: 'scale(0)' }
  })

  // This will orchestrate the two animations above, comment the last arg and it creates a sequence
  useChain(open ? [springRef, transRef] : [transRef, springRef], [0, open ? 0.1 : 0.6])

  return (
    <div id="root" style={{alignContent:"center" ,alignItems:"center", }} >
      {open?<button  onClick={() => {console.log("5555555555555555") ; set(open=>false); console.log(open,"ipen")}}>close</button>:null}
     <Global></Global>
      <Container style={{ ...rest, width: size, height: size }} onClick={() => set(open => true)}>
        {transitions.map(({ item, key, props }) => (
          <Item key={key} style={{ ...props, background: item.css }} >hiiiiiiiiiiiii</Item>
        ))}
        <div > {open?"":"My Projects"}</div>
        {/* <button style={{position:"fixed" , top:"20"}} onClick={() => set(open => !open)} > {open?"close":""}</button> */}
      </Container>
    </div>
  )
}

export default Projects
